.form__header {
  margin: 20 auto;
  text-align: center;
}

.form__labelText {
  width: 100%;
  max-width: 200px;
  display: inline-block;
  margin-right: 10px;
}

.form__field {
  width: 100%;
  border: none;
  border-radius: 8px;
  padding: 12px;
}

.form__button {
  display: grid;
  grid-template-columns: 1fr;
  width: 50%;
  padding: 12px 32px;
  margin: 0 auto;
  border-radius: 4px;
  background-color: hsl(0, 0%, 0%);
  color: hsl(0, 0%, 100%);
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out, transform 0.3s linear;
}

.form__button:hover {
  background-color: hsl(0, 0%, 20%);
  transform: scale(1.2);
}

.form__button:active {
  background-color: hsl(0, 0%, 40%);
}

.form__fieldset {
  background-color: hsl(0, 0%, 100%);
  border: none;
  padding: 10px;
  border-radius: 12px;
  max-width: 600px;
}

.form__legend {
  margin: 0 auto;
  padding: 12px 20px;
  border-radius: 5px;
  color: hsl(0, 0%, 26%);
  background-color: hsl(0, 0%, 85%);
  box-shadow: 0 2px 6px 0 hsla(0, 0%, 80%, 0.2);
}

.form__paragraph {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

.form__info {
  text-align: center;
  padding: 8px;
  font-size: 16px;
  color: hsl(0, 0%, 31%);
}

.form__span {
  padding-left: 8px;
  color: hsl(0, 0%, 17%);
}

.form__buttons {
  display: grid;
  grid-template-columns: 100px 100px;
  grid-gap: 52px;
  justify-content: center;
}

.form__footer {
  text-align: center;
  padding: 20px;
  margin: 0 auto;
  max-width: 300px;
}

.form__link {
  color: hsl(0, 0%, 73%);
}